}

.loader {
  height: 25px;
  width: 200px;
  background: #fff;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  height: 100%;
  width: 0;
  background: #ff3d00;
  animation: prog 6s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes prog {
  to {
    width: 100%;
  }
}

.display-non {
  display: none;
}

